<template>
  <div>
    <el-card class="box-card">
      <div class="clearfix" slot="header">
        <div classs="header">所有试题</div>
      </div>
      <!-- 表格部分 -->
      <el-button @click="advence_box_search" round type="primary">高级检索</el-button>
      <el-button @click="importDialogVisible=!importDialogVisible" round type="success">导入试题</el-button>

      <!-- 高级检索的开始 -->
      <transition name="el-fade-in-linear">
        <div class="search_box" v-show="show">
          <el-checkbox-group :max="1" :min="0" class="advence_checkbox" v-model="advencd_state">
            <el-checkbox
              :key="state.index"
              :label="state"
              @change="box_change"
              v-for="state in exam_state"
            >{{state}}</el-checkbox>
            <el-button
              @click="adv_search"
              circle
              class="advence_btn"
              icon="el-icon-search"
              size="small"
            ></el-button>
          </el-checkbox-group>
        </div>
      </transition>
      <!-- 高级检索结束  -->

      <!-- 导入试题的dialog -->

      <el-dialog
        :close-on-click-modal="false"
        :modal-append-to-body="false"
        :visible.sync="importDialogVisible"
        class="import"
        title="批量导入试题"
        width="30%"
      >
        <span>操作步骤</span>
        <p class="tipsInfo">
          1,下载《批量导入模板》
          <br />2,打开下载表，将对应字段信息输入或粘贴进本表。为保障粘贴信息被有效导入，请使用纯文本或数字
          <br />3、信息输入完毕，点击 “浏览” 按钮， 选择excel文档
          <br />4、点击 “开始导入” ，导入中如有任何疑问，请联系 企划部
        </p>
        <a @click="getExcelTemplate">点击下载导入模板</a>
        <el-upload
          :auto-upload="false"
          :file-list="fileList"
          :on-error="uploadError"
          :on-success="uploadSuccess"
          accept=".xls"
          action="http://192.168.0.115:8081/examination/import"
          class="myUpload upload-demo"
          ref="upload"
        >
          <el-button size="small" slot="trigger" type="primary">选取文件</el-button>
        </el-upload>
        <span class="dialog-footer" slot="footer">
          <el-button @click="importDialogVisible = false">取 消</el-button>
          <el-button @click="submitUpload" type="success">上传到服务器</el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-table :data="questions" border style="width: 100%">
        <el-table-column label="题干" prop="question" width="400"></el-table-column>
        <el-table-column label="类型" width="120">
          <template slot-scope="scope">{{ formatExamType(scope.row.type)}}</template>
        </el-table-column>
        <el-table-column label="选项A" prop="ans1" width="120"></el-table-column>
        <el-table-column label="选项B" prop="ans2" width="120"></el-table-column>
        <el-table-column label="选项C" prop="ans3" width="120"></el-table-column>
        <el-table-column label="选项D" prop="ans4" width="120"></el-table-column>
        <el-table-column label="正确答案" prop="res" width="120"></el-table-column>
        <el-table-column
          :filter-method="filterTag"
          :filters="[{ text: '上线', value: 1}, { text: '待审核', value: 0},{ text: '下线', value: 2 }]"
          label="状态"
          width="120"
        >
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.state === 0? 'info' : scope.row.state === 1?'success':'danger'"
              disable-transitions
            >{{formatExamState(scope.row.state)}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="类别" prop="category"></el-table-column>
        <el-table-column fixed="right" label="操作">
          <template slot-scope="scope">
            <el-button @click="showDialog(scope.row)" size="small">编辑</el-button>
            <el-button @click="deleteExamination(scope.row)" size="small" type="danger">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 更新试题的dialog -->
      <el-dialog
        :before-close="handClose"
        :dialogVisible="false"
        :visible.sync="dialogVisible"
        append-to-body
        title="试题详情"
        width="30%"
      >
        <el-form :model="tempForm" :rules="rules" label-width="80px" ref="updateForm">
          <el-form-item label="题干" prop="question">
            <el-input v-model="tempForm.question"></el-input>
          </el-form-item>
          <el-form-item label="类型" prop="type ">
            <el-radio :label="0" v-model="tempForm.type">单选</el-radio>
            <el-radio :label="1" v-model="tempForm.type">判断</el-radio>
            <el-radio :label="2" v-model="tempForm.type">多选</el-radio>
          </el-form-item>
          <el-form-item label="选项A" prop="ans1">
            <el-input v-model="tempForm.ans1"></el-input>
          </el-form-item>
          <el-form-item label="选项B" prop="ans2">
            <el-input v-model="tempForm.ans2"></el-input>
          </el-form-item>
          <el-collapse-transition>
            <div v-show="!(tempForm.type===1)">
              <el-form-item class="transition-box" label="选项C">
                <el-input v-model="tempForm.ans3"></el-input>
              </el-form-item>
              <el-form-item class="transition-box" label="选项D">
                <el-input v-model="tempForm.ans4"></el-input>
              </el-form-item>
            </div>
          </el-collapse-transition>
          <el-form-item label="正确答案" prop="res">
            <el-checkbox-group :max="(tempForm.type===2)?4:1" :min="0" v-model="tempForm.res">
              <el-checkbox :key="n.index" :label="n" v-for="n in ['A','B']">选项{{n}}</el-checkbox>
              <el-checkbox
                :key="t.index"
                :label="t"
                v-for="t in ['C','D']"
                v-show="!(tempForm.type===1)"
              >选项{{t}}</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="状态">
            <el-tooltip :content="'Switch value: ' + tempForm.state" placement="top">
              <el-switch
                :active-value="1"
                :inactive-value="2"
                active-color="#13ce66"
                inactive-color="#ff4949"
                v-model="tempForm.state"
              ></el-switch>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="类别" prop="cid">
            <el-select placeholder="请选择" v-model="tempForm.cid">
              <el-option
                :key="item.id"
                :label="item.title"
                :value="item.id"
                v-for="item in categoryList"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="备注" prop="remark">
            <el-input :value="tempForm.remark" disabled></el-input>
          </el-form-item>
        </el-form>
        <span class="dialog-footer" slot="footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button @click="updateShiTi('updateForm')" type="primary">确认更新</el-button>
        </span>
      </el-dialog>
      <!-- dialog结束 -->

      <!-- 分页插件 -->
      <div class="page">
        <el-pagination
          :current-page="currentpage"
          :total="total"
          @current-change="changedPage"
          background
          layout="prev, pager, next"
        ></el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>

export default {

  data () {
    return {
      questions: null,
      currentpage: 1,
      total: -1,
      categoryList: null,
      dialogVisible: false,
      tttt: [],
      tempForm: {
        question: '',
        type: -1,
        ans1: '',
        ans2: '',
        ans3: '',
        ans4: '',
        state: '',
        res: [],
        cid: ''
      },
      rules: {
        question: [
          { required: true, message: '请输入题干', trigger: 'blur' }],
        type: [{ required: true, message: '请选择类型', trigger: 'blur' }],
        cid: [{ required: true, message: '请选择题目分类', trigger: 'blur' }],
        res: [{ required: true, message: '请输入正确答案', trigger: 'blur' }]
      },
      advencd_state: [], // 高级搜索中的state，用于只看上线/下线/待审核的试题
      exam_state: ['上线', '下线', '待审核'],
      show: false,
      advencd_search_state: null,
      importDialogVisible: false,
      // url: 'http://192.168.0.115:8081',
      url: 'http://118.25.209.141:8081',
      fileList: [],
      delExamDialog: false// 删除试题对话框的flag
    }
  },
  created () {
    this.getShiTiList();
    this.getExamCategory();
  },
  methods: {
    // 删除提示

    deleteExamination (row) {
      var _this = this;
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        _this.deleteRequest('/examination/' + row.id).then(res => {
          if (res.status === 200) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            _this.getShiTiList();
          } else {
            this.$message({
              type: 'success',
              message: '删除失败!'
            });
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    uploadSuccess (response) {
      this.$notify({
        'title': '通知',
        'message': response.data
      });
      this.getShiTiList();
    },
    uploadError (response) {
      this.$notify({
        'title': '通知',
        'message': '导入失败,请联系管理员'

      });
    },

    submitUpload () {
      this.$refs.upload.submit();

      this.importDialogVisible = false;
    },

    box_change () {
      // 当复选框改变时,需要改变当前的页码数 currentpage
      this.currentpage = 1;
    },
    getExcelTemplate () {
      window.open(this.url + '/examination/excelTemplate', '_blank');
    },

    advence_box_search () {
      this.show = !this.show;
      this.advencd_search_state = null;
      this.advencd_state = [];
      if (!this.show) {
        this.getShiTiList();
      }
    },

    adv_search () {
      let state = this.advencd_state[0];
      switch (state) {
        case '上线':
          this.advencd_search_state = 1;
          break;
        case '待审核':
          this.advencd_search_state = 0;
          break;
        case '下线':
          this.advencd_search_state = 2;
          break;
      }
      this.getShiTiList();
    },
    filterTag (value, row) {
      return row.state === value;
    },
    // 更新试题
    updateShiTi (formName) {
      let _this = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          _this.tempForm.res = this.tempForm.res.join(',');
          _this.postRequest('/examination/update', _this.tempForm).then(res => {
            console.log(res);
            if (res.data.status === 200) {
              _this.$message({
                showClose: true,
                message: ' 更新成功',
                type: 'success'
              });
            }
            _this.dialogVisible = false;
            _this.getShiTiList();
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    // 再关闭之间,把正确答案转换成正确的格式,为了下一次的点击编辑能正确显示
    handClose (done) {
      this.tempForm.res = this.tempForm.res.join(',');
      done();
    },

    // 获取分类的集合
    getExamCategory () {
      var _this = this;
      this.getRequest('/exam-category/all').then(res => {
        _this.categoryList = res.data.data;
      })
    },
    // 获取试题列表
    getShiTiList () {
      var _this = this;
      let url = '/examination/list?pageNum=' + _this.currentpage;
      if (this.advencd_search_state !== null) {
        url += '&state=' + _this.advencd_search_state;
      }

      this.getRequest(url).then(res => {
        _this.currentpage = res.data.data.current;
        _this.total = res.data.data.total;
        _this.questions = res.data.data.records;
      })
    },
    // 编辑每行的数据
    showDialog (row) {
      this.dialogVisible = true;
      console.log(row);

      this.tempForm = row;
      this.tempForm.res = row.res.split(',');
    },
    // 格式化类型
    formatExamType (val) {
      return val === 0 ? '单选' : (val === 1 ? '判断' : '多选');
    },
    // 格式化试题状态
    formatExamState (val) {
      return val === 0 ? '待审核' : (val === 1 ? '上线' : '下线');
    },
    // 改变页数
    changedPage (changedPage) {
      this.currentpage = changedPage;
      this.getShiTiList();
    }

  }

}
</script>
<style>
.header {
  margin-bottom: 6px;
}
.page {
  text-align: center;
}
.search_box {
  height: 60px;
  border: 1px solid rgb(32, 160, 255);
  border-radius: 5px;
  margin-bottom: 5px;
}
.advence_btn {
  margin-top: 5px;
  margin-left: 10px;
}
.advence_checkbox {
  margin-top: 5px;
  margin-left: 10px;
}

.import .tipsInfo {
  margin-top: 5px;
  font-size: 14px;
  line-height: 30px;
}
.import span {
  font-size: 18;
}
.import a {
  display: inline-block;
  margin-top: 10px;
  cursor: pointer;
  color: #3487e2;
}
.import a:link {
  /* link选择器,选择未被访问的链接,并设置其样式 */
  text-decoration: none;
}
.import a:visited {
  /* 选择已访问的链接，并设置其样式： */
  text-decoration: none;
}
.import a:hover {
  /*  悬浮*/
  text-decoration: underline;
  color: #3487e2;
}
.import a:active {
  /* 活动的链接, 当点击时按下去的时候 */
  text-decoration: none;
}
.import .myUpload {
  margin-top: 10px;
}
</style> 


